async function handleNext() {
    const stepper = processEditorState.value.stepper;
    const selectedExistingBpmnFile = processModel.value.selectedExistingBpmnFile;
    const currentPage = stepper.currentStep;
    if (stepper.isTabValid) {
        stepper.currentStep++;
        stepper.isTabValid = false;
        props.callbacks.modified({ state: true });
    }
    resizeWizard();
    processVariablesEditable.value =
        processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn && stepper.currentStep == 1;

    if (stepper.currentStep == 4) {
        writeProcessModelDataToProcessVariablesTable();
    } else if (currentPage == 4) {
        writeProcessVariablesTableDataToProcessModel();
    }
}

function handleBack() {
    if (processEditorState.value.stepper!.currentStep == 4) {
        writeProcessVariablesTableDataToProcessModel();
    }
    processEditorState.value.stepper!.currentStep--;
    processEditorState.value.stepper!.isTabValid = false;
    processVariablesEditable.value =
        processModel.value.usingExistingBpmnFile &&
        processEditorState.value.selectedExistingBpmn &&
        processEditorState.value.stepper.currentStep == 1;
    props.callbacks.modified({ state: true });
    resizeWizard();
}

function handleSave() {
	if (!checkDeletedScreens()){
		processEditorState.value.stepper.currentStep = 1;
		resizeWizard();
		return;
	}
    const processModelItem = viewModel.studio.currentItem!;
    addRowVariablesToModel();
    if (processModel.value.processState == "CREATE" && !processModel.value.usingExistingBpmnFile) {
        const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.processFileName);
        executor.execute(
            async () => {
                const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {
                    name: processModel.value.processFlowName || processEditorState.value.processFileName,
                    parentObjectId: currentModule.value.ID,
                    modelType: "bpmn",
                    modelBody: [{ key: "bpmn", model: bpmnFile }],
                    modelAdditionals: {},
                });
                processModel.value.processState = "UPDATE";
                processModel.value.selectedExistingBpmnFile.ID = createdItem.ID;
                modelsOfModule.value.bpmns.push(createdItem);
                disableFinishButton.value = true;
                await studio.save(processModelItem);
                processEditorState.value.stepper.currentStep = 1;
                resizeWizard();
                studio.openItem(createdItem, { fromHistory: false, noFocus: true });
            },
            { loading: true }
        );
    } else {
        disableFinishButton.value = true;
        executor.execute(
            async () => {
                disableFinishButton.value = true;
                processModel.value.processState = "UPDATE";
                await studio.save(processModelItem); 
                processEditorState.value.stepper.currentStep = 1;
                resizeWizard();
            },
            { loading: true }
        );
    }
    props.callbacks.modified({ state: true });
}

function handleDiscard() {
    executor.execute(
        async () => {
            if (viewModel.studio.currentItem?.modified.model == undefined) updateProcessVariablesTableRowsCache();
            else
                processEditorState.value.processVariablesTableRows = JSON.parse(
                    JSON.stringify(processEditorState.value.processVariablesTableRowsCache)
                );
            await studio.release(viewModel.studio.currentItem!);
        },
        { loading: true }
    );
}

function resizeWizard() {
