function addApprovalVariablesIntoTableRows() {
    if (processModel.value.processType == "single_approval") {
        deleteApprovalVariablesRowByProcessType(["2", "3", "4", "5"]);
    } else if (processModel.value.processType == "double_approval") {
        addApprovalVariablesByProcessType([2]);
        deleteApprovalVariablesRowByProcessType(["3", "4", "5"]);
    } else if (processModel.value.processType == "triple_approval") {
        addApprovalVariablesByProcessType([2, 3]);
        deleteApprovalVariablesRowByProcessType(["4", "5"]);
    } else if (processModel.value.processType == "quadruple_approval") {
        addApprovalVariablesByProcessType([2, 3, 4]);
        deleteApprovalVariablesRowByProcessType(["5"]);
    } else if (processModel.value.processType == "quintuple_approval") {
        addApprovalVariablesByProcessType([2, 3, 4, 5]);
    }
}

function addApprovalVariablesByProcessType(steps: Array<number>) {
    steps.forEach((step) => {
        if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == `supportingForm${step}`))
            processEditorState.value.processVariablesTableRows.push({
                parameter: `supportingForm${step}`,
                currentValue: "",
                defaultValue: "",
                dataType: "Screen",
                defaultDataType: "Screen",
                isDeletable: false,
            });
        if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == `checkerRoles${step}`)) {
            const checkerRole =
                processEditorState.value.processName +
                (step == 2
                    ? ".secondChecker"
                    : step == 3
                    ? ".thirdChecker"
                    : step == 4
                    ? ".fourthChecker"
                    : step == 5
                    ? ".fifthChecker"
                    : ".checker");
            processEditorState.value.processVariablesTableRows.push({
                parameter: `checkerRoles${step}`,
                currentValue: checkerRole,
                defaultValue: checkerRole,
                dataType: "String",
                defaultDataType: "String",
                isDeletable: false,
            });
        }
    });
}

function deleteApprovalVariablesRowByProcessType(steps: Array<string>) {
    processEditorState.value.processVariablesTableRows = processEditorState.value.processVariablesTableRows.filter(
        (row) =>
            !(
                steps.includes(row.parameter.charAt(row.parameter.length - 1)) &&
                ((row.parameter.startsWith("checkerRoles") && row.parameter.length == 13) ||
                    (row.parameter.startsWith("supportingForm") && row.parameter.length == 15))
            )
    );
}

function addRowVariablesToModel() {
    writeProcessModelDataToProcessVariablesTable();
    writeProcessVariablesTableDataToProcessModel();
    processEditorState.value.processVariablesTableRows.forEach((row) => (row.defaultValue = row.currentValue));
    debugger;
    processModel.value.selectedExistingBpmnFile.variables = processEditorState.value.processVariablesTableRows.map((variable) => ({
        isDeletable: variable.isDeletable,
        key: variable.parameter,
        type: variable.dataType,
        value: variable.currentValue,
    }));
}

