___CSS_LOADER_EXPORT___.push([module.id, "\n.process-editor[data-v-43e90339] {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n    transition: height 250ms ease-in-out;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/editors/processEditor/processEditor.vue"],"names":[],"mappings":";AAilBA;IACI,aAAa;IACb,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,sCAAsC;IACtC,gBAAgB;IAChB,yBAAyB;IACzB,kBAAkB;IAClB,oCAAoC;AACxC","sourcesContent":["<script setup lang=\"ts\">\nimport { IProcessEditorProps } from \"../../../../application/objects/editors/processEditorImpl\";\nimport { ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { ICreateModel } from \"../../../../domain/useCase/ICreateModel\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\n\nimport { IModule } from \"../../../../domain/model/models\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IProcessEditorUtil } from \"../../../../domain/useCase/IProcessEditorUtil\";\nimport { IModelsOfModule } from \"../../../../domain/presentation/editors/IProcessEditorModel\";\nimport ProcessEditorHeader from \"./components/processEditorHeader.vue\";\nimport ProcessEditorFooter from \"./components/processEditorFooter.vue\";\nimport ScreenSelectionTab from \"./tabs/screenSelectionTab.vue\";\nimport ServiceCallConfigurationTab from \"./tabs/serviceCallConfigurationTab.vue\";\nimport AdvancedSettingsTab from \"./tabs/advancedSettings.vue\";\nimport ProcessVariablesTab from \"./tabs/processVariablesTab.vue\";\nimport SummaryTab from \"./tabs/summaryTab.vue\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n\ninterface IProps {\n    functions: IProcessEditorProps[\"functions\"];\n    callbacks: IProcessEditorProps[\"callbacks\"];\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst props = defineProps<IProps>();\nconst renderKey = ref(\"\");\nconst disableFinishButton = ref(false);\nconst title = ref(\"Process Wizard\");\nconst wizardHeight = ref(\"700px\");\n\nlet initialized = ref(false);\n\nconst processModel = ref(processEditorUtil.getEmptyProcessWizardModel(\"_\"));\nconst processEditorState = ref(processEditorUtil.getEmptyProcessEditorState());\nconst currentModule = ref({} as IModule);\nconst modelsOfModule = ref({\n    screens: [],\n    bpmns: [],\n} as IModelsOfModule);\nconst processVariablesEditable = ref(true);\n\nprops.functions.getModel = async () => {\n    addRowVariablesToModel();\n    return { model: processModel.value, state: processEditorState.value };\n};\n\nprops.functions.setModel = async (model, module, state) => {\n    currentModule.value = module;\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(module);\n    renderKey.value = CryptoHelper.CreateGuid();\n    processModel.value = model;\n    processEditorState.value = state;\n    initialized.value = true;\n    resizeWizard();\n};\n\nfunction checkDeletedScreens(){\n\tconst stepper = processEditorState.value.stepper;\n\tmodelsOfModule.value = processEditorUtil.getModelsOfModule(viewModel.studio.currentItem?.parent as IModule);\n\tlet returnFlag = true;\n\tlet checkScreenID = processModel.value.processMakerDefinition.makerScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processMakerDefinition.makerScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.processCheckerDefinition.firstApproverScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processCheckerDefinition.firstApproverScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.processCheckerDefinition.secondApproverScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processCheckerDefinition.secondApproverScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.processCheckerDefinition.thirdApproverScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processCheckerDefinition.thirdApproverScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.processCheckerDefinition.fourthApproverScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processCheckerDefinition.fourthApproverScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.processCheckerDefinition.fifthApproverScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.processCheckerDefinition.fifthApproverScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\tcheckScreenID = processModel.value.reviewScreenID;\n\tif(checkScreenID.length > 0 && (modelsOfModule.value.screens.find(screen => screen.ID == checkScreenID) == undefined)){\n\t\tprocessModel.value.reviewScreenID = \"\";\n\t\treturnFlag = false;\n\t}\n\n\treturn returnFlag;\n}\n\nasync function handleNext() {\n    const stepper = processEditorState.value.stepper;\n    const selectedExistingBpmnFile = processModel.value.selectedExistingBpmnFile;\n    const currentPage = stepper.currentStep;\n    if (stepper.isTabValid) {\n        stepper.currentStep++;\n        stepper.isTabValid = false;\n        props.callbacks.modified({ state: true });\n    }\n    resizeWizard();\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn && stepper.currentStep == 1;\n\n    if (stepper.currentStep == 4) {\n        writeProcessModelDataToProcessVariablesTable();\n    } else if (currentPage == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n}\n\nfunction handleBack() {\n    if (processEditorState.value.stepper!.currentStep == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n    processEditorState.value.stepper!.currentStep--;\n    processEditorState.value.stepper!.isTabValid = false;\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile &&\n        processEditorState.value.selectedExistingBpmn &&\n        processEditorState.value.stepper.currentStep == 1;\n    props.callbacks.modified({ state: true });\n    resizeWizard();\n}\n\nfunction handleSave() {\n\tif (!checkDeletedScreens()){\n\t\tprocessEditorState.value.stepper.currentStep = 1;\n\t\tresizeWizard();\n\t\treturn;\n\t}\n    const processModelItem = viewModel.studio.currentItem!;\n    addRowVariablesToModel();\n    if (processModel.value.processState == \"CREATE\" && !processModel.value.usingExistingBpmnFile) {\n        const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.processFileName);\n        executor.execute(\n            async () => {\n                const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {\n                    name: processModel.value.processFlowName || processEditorState.value.processFileName,\n                    parentObjectId: currentModule.value.ID,\n                    modelType: \"bpmn\",\n                    modelBody: [{ key: \"bpmn\", model: bpmnFile }],\n                    modelAdditionals: {},\n                });\n                processModel.value.processState = \"UPDATE\";\n                processModel.value.selectedExistingBpmnFile.ID = createdItem.ID;\n                modelsOfModule.value.bpmns.push(createdItem);\n                disableFinishButton.value = true;\n                await studio.save(processModelItem);\n                processEditorState.value.stepper.currentStep = 1;\n                resizeWizard();\n                studio.openItem(createdItem, { fromHistory: false, noFocus: true });\n            },\n            { loading: true }\n        );\n    } else {\n        disableFinishButton.value = true;\n        executor.execute(\n            async () => {\n                disableFinishButton.value = true;\n                processModel.value.processState = \"UPDATE\";\n                await studio.save(processModelItem); \n                processEditorState.value.stepper.currentStep = 1;\n                resizeWizard();\n            },\n            { loading: true }\n        );\n    }\n    props.callbacks.modified({ state: true });\n}\n\nfunction handleDiscard() {\n    executor.execute(\n        async () => {\n            if (viewModel.studio.currentItem?.modified.model == undefined) updateProcessVariablesTableRowsCache();\n            else\n                processEditorState.value.processVariablesTableRows = JSON.parse(\n                    JSON.stringify(processEditorState.value.processVariablesTableRowsCache)\n                );\n            await studio.release(viewModel.studio.currentItem!);\n        },\n        { loading: true }\n    );\n}\n\nfunction resizeWizard() {\n    if (\n        processEditorState.value.stepper.currentStep == 4 ||\n        processEditorState.value.stepper.currentStep == 5 ||\n        !viewModel.studio.currentItem?.checkedOut\n    ) {\n        wizardHeight.value = \"886px\";\n    } else {\n        wizardHeight.value = \"700px\";\n    }\n}\n\nfunction updateProcessVariablesTableRowsCache() {\n    processEditorState.value.processVariablesTableRowsCache = JSON.parse(JSON.stringify(processEditorState.value.processVariablesTableRows));\n}\n\nfunction writeProcessModelDataToProcessVariablesTable() {\n    if (!processModel.value.usingExistingBpmnFile && processModel.value.processState != \"UPDATE\") {\n        addApprovalVariablesIntoTableRows();\n    }\n    processEditorState.value.processVariablesTableRows.forEach((row) => {\n        switch (row.parameter) {\n            case \"isRunAfterApprovalEnable\":\n                row.currentValue = Boolean(processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable);\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterApprovalApi\":\n                row.currentValue = processModel.value.runAfterApprovalDefinition.runAfterApprovalApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterApprovalMethod\":\n                row.currentValue = processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                row.currentValue = Boolean(processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable);\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                row.currentValue = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                row.currentValue = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                row.currentValue = Boolean(processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable);\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterRejectedApi\":\n                row.currentValue = processModel.value.runAfterRejectedDefinition.runAfterRejectedApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterRejectedMethod\":\n                row.currentValue = processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"useHierarchicalProcess\":\n                row.currentValue = Boolean(processModel.value.processMakerDefinition.useHierarchicalProcess);\n                row.dataType = row.defaultDataType;\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                row.currentValue = Boolean(processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit);\n                row.dataType = row.defaultDataType;\n                break;\n            case \"instUnitHierarchy\":\n                row.currentValue = processModel.value.processMakerDefinition.instUnitHierarchy;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"startForm\":\n                row.currentValue = processModel.value.processMakerDefinition.makerScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm\":\n                row.currentValue = processModel.value.processCheckerDefinition.firstApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm2\":\n                row.currentValue = processModel.value.processCheckerDefinition.secondApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm3\":\n                row.currentValue = processModel.value.processCheckerDefinition.thirdApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm4\":\n                row.currentValue = processModel.value.processCheckerDefinition.fourthApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm5\":\n                row.currentValue = processModel.value.processCheckerDefinition.fifthApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"reviewForm\":\n                row.currentValue = processModel.value.reviewScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            default:\n                break;\n        }\n\n        \n    });\n\n    processEditorState.value.processVariablesTableRowsCache = JSON.parse(JSON.stringify(processEditorState.value.processVariablesTableRows));\n}\n\nfunction writeProcessVariablesTableDataToProcessModel() {\n    processEditorState.value.processVariablesTableRows.forEach((row) => {\n        switch (row.parameter) {\n            case \"isRunAfterApprovalEnable\":\n                if (row.dataType == \"Boolean\") processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable = Boolean(row.currentValue);\n                break;\n            case \"runAfterApprovalApi\":\n                if (row.dataType == \"String\") processModel.value.runAfterApprovalDefinition.runAfterApprovalApi = row.currentValue as string;\n                break;\n            case \"runAfterApprovalMethod\":\n                if (row.dataType == \"Http Method\") processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod = row.currentValue as string;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                if (row.dataType == \"Boolean\")\n                    processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable = Boolean(row.currentValue);\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                if (row.dataType == \"String\")\n                    processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi = row.currentValue as string;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                if (row.dataType == \"Http Method\")\n                    processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod = row.currentValue as string;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                if (row.dataType == \"Boolean\") processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable = Boolean(row.currentValue);\n                break;\n            case \"runAfterRejectedApi\":\n                if (row.dataType == \"String\") processModel.value.runAfterRejectedDefinition.runAfterRejectedApi = row.currentValue as string;\n                break;\n            case \"runAfterRejectedMethod\":\n                if (row.dataType == \"Http Method\") processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod = row.currentValue as string;\n                break;\n            case \"useHierarchicalProcess\":\n                if (row.dataType == \"Boolean\") processModel.value.processMakerDefinition.useHierarchicalProcess = Boolean(row.currentValue);\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                if (row.dataType == \"Boolean\") processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit = Boolean(row.currentValue);\n                break;\n            case \"instUnitHierarchy\":\n                if (row.dataType == \"String\" && (row.currentValue == \"SAME\" || row.currentValue == \"PARENT\"))\n                    processModel.value.processMakerDefinition.instUnitHierarchy = row.currentValue as string;\n                break;\n            case \"startForm\":\n                if (row.dataType == \"Screen\") processModel.value.processMakerDefinition.makerScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.firstApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm2\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.secondApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm3\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.thirdApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm4\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.fourthApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm5\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.fifthApproverScreenID = row.currentValue as string;\n                break;\n            case \"reviewForm\":\n                if (row.dataType == \"Screen\") processModel.value.reviewScreenID = row.currentValue as string;\n                break;\n            case \"businessKey\":\n            case \"checkerRoles\":\n            case \"checkerRoles2\":\n            case \"checkerRoles3\":\n            case \"checkerRoles4\":\n            case \"checkerRoles5\":\n            case \"entityId\":\n            case \"resourceCode\":\n            case \"tenantId\":\n                if (row.dataType != \"String\") {\n                    row.dataType = row.defaultDataType;\n                    row.currentValue = row.defaultValue;\n                }\n                break;\n            case \"reviewStep\":\n                if (row.dataType != \"Boolean\") {\n                    row.dataType = row.defaultDataType;\n                    row.currentValue = Boolean(row.defaultValue);\n                }\n                break;\n            case \"selectApproverForm\":\n                if (row.dataType != \"Screen\") {\n                    row.dataType = row.defaultDataType;\n                    row.currentValue = row.defaultValue;\n                }\n                break;\n            default:\n                break;\n        }\n    });\n}\n\nfunction addApprovalVariablesIntoTableRows() {\n    if (processModel.value.processType == \"single_approval\") {\n        deleteApprovalVariablesRowByProcessType([\"2\", \"3\", \"4\", \"5\"]);\n    } else if (processModel.value.processType == \"double_approval\") {\n        addApprovalVariablesByProcessType([2]);\n        deleteApprovalVariablesRowByProcessType([\"3\", \"4\", \"5\"]);\n    } else if (processModel.value.processType == \"triple_approval\") {\n        addApprovalVariablesByProcessType([2, 3]);\n        deleteApprovalVariablesRowByProcessType([\"4\", \"5\"]);\n    } else if (processModel.value.processType == \"quadruple_approval\") {\n        addApprovalVariablesByProcessType([2, 3, 4]);\n        deleteApprovalVariablesRowByProcessType([\"5\"]);\n    } else if (processModel.value.processType == \"quintuple_approval\") {\n        addApprovalVariablesByProcessType([2, 3, 4, 5]);\n    }\n}\n\nfunction addApprovalVariablesByProcessType(steps: Array<number>) {\n    steps.forEach((step) => {\n        if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == `supportingForm${step}`))\n            processEditorState.value.processVariablesTableRows.push({\n                parameter: `supportingForm${step}`,\n                currentValue: \"\",\n                defaultValue: \"\",\n                dataType: \"Screen\",\n                defaultDataType: \"Screen\",\n                isDeletable: false,\n            });\n        if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == `checkerRoles${step}`)) {\n            const checkerRole =\n                processEditorState.value.processName +\n                (step == 2\n                    ? \".secondChecker\"\n                    : step == 3\n                    ? \".thirdChecker\"\n                    : step == 4\n                    ? \".fourthChecker\"\n                    : step == 5\n                    ? \".fifthChecker\"\n                    : \".checker\");\n            processEditorState.value.processVariablesTableRows.push({\n                parameter: `checkerRoles${step}`,\n                currentValue: checkerRole,\n                defaultValue: checkerRole,\n                dataType: \"String\",\n                defaultDataType: \"String\",\n                isDeletable: false,\n            });\n        }\n    });\n}\n\nfunction deleteApprovalVariablesRowByProcessType(steps: Array<string>) {\n    processEditorState.value.processVariablesTableRows = processEditorState.value.processVariablesTableRows.filter(\n        (row) =>\n            !(\n                steps.includes(row.parameter.charAt(row.parameter.length - 1)) &&\n                ((row.parameter.startsWith(\"checkerRoles\") && row.parameter.length == 13) ||\n                    (row.parameter.startsWith(\"supportingForm\") && row.parameter.length == 15))\n            )\n    );\n}\n\nfunction addRowVariablesToModel() {\n    writeProcessModelDataToProcessVariablesTable();\n    writeProcessVariablesTableDataToProcessModel();\n    processEditorState.value.processVariablesTableRows.forEach((row) => (row.defaultValue = row.currentValue));\n    debugger;\n    processModel.value.selectedExistingBpmnFile.variables = processEditorState.value.processVariablesTableRows.map((variable) => ({\n        isDeletable: variable.isDeletable,\n        key: variable.parameter,\n        type: variable.dataType,\n        value: variable.currentValue,\n    }));\n}\n</script>\n<template>\n    <div class=\"process-editor\" :style=\"'height:' + wizardHeight\" v-if=\"initialized\" :key=\"renderKey\">\n        <ProcessEditorHeader :state=\"processEditorState\" :title=\"title\" :process-state=\"processModel.processState\" />\n        <div v-if=\"!viewModel.studio.currentItem?.checkedOut\">\n            <SummaryTab :model=\"processModel\" :state=\"processEditorState\" :screens=\"modelsOfModule.screens\" :modified=\"props.callbacks.modified\" />\n        </div>\n        <div v-else>\n            <ScreenSelectionTab\n                v-if=\"processEditorState.stepper.currentStep === 1\"\n                :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\"\n                :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\"\n            />\n            <ServiceCallConfigurationTab\n                v-if=\"processEditorState.stepper.currentStep === 2\"\n                :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\"\n                :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\"\n            />\n            <AdvancedSettingsTab\n                v-if=\"processEditorState.stepper.currentStep === 3\"\n                :model=\"processModel\"\n                :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\"\n            />\n            <ProcessVariablesTab\n                v-if=\"processEditorState.stepper.currentStep == 4\"\n                :editable=\"true\"\n                :state=\"processEditorState\"\n                :screens=\"modelsOfModule.screens\"\n                :modified=\"props.callbacks.modified\"\n                :model=\"processModel\"\n            />\n            <SummaryTab\n                v-if=\"processEditorState.stepper.currentStep === 5\"\n                :model=\"processModel\"\n                :state=\"processEditorState\"\n                :screens=\"modelsOfModule.screens\"\n                :modified=\"props.callbacks.modified\"\n            />\n        </div>\n        <div></div>\n        <ProcessEditorFooter\n            :handle-next=\"handleNext\"\n            :handle-back=\"handleBack\"\n            :handle-save=\"handleSave\"\n            :handle-discard=\"handleDiscard\"\n            :resize-wizard=\"resizeWizard\"\n            :state=\"processEditorState\"\n            :model=\"processModel\"\n        />\n    </div>\n    <!-- <div class=\"process-editor\" v-if=\"false\">\n        <ProcessDefinitionTab v-if=\"processEditorState.stepper.currentStep === 0\" :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\" :state=\"processEditorState\" :modified=\"props.callbacks.modified\"\n            :enable-finish-button=\"() => (disableFinishButton = false)\" :is-next-active=\"isNextActive\"\n            :key=\"processDefinitionTabKey\" />\n        <EditProcessVariablesTab\n            v-if=\"processModel.processState == 'CREATE' && processEditorState.stepper.currentStep === 1\"\n            :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <SingleApprovalWizard v-if=\"\n            processModel.processType === SINGLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <DoubleApprovalWizard v-if=\"\n            processModel.processType === DOUBLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <TripleApprovalWizard v-if=\"\n            processModel.processType === TRIPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <QuadrupleApprovalWizard v-if=\"\n            processModel.processType === QUADRUPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <QuintupleApprovalWizard v-if=\"\n            processModel.processType === QUINTUPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <div>\n            <button style=\"margin-bottom: 5%\" :disabled=\"!processEditorState.stepper.currentStep\"\n                @click=\"handleBack\">Back</button>\n            <button v-if=\"\n                !processEditorState.stepper.isApproveTab && processModel.processState != 'CREATE' && processEditorState.stepper.currentStep === 1\n            \" @click=\"handleNext\" :disabled=\"!processEditorState.stepper.isTabValid\">\n                Next\n            </button>\n            <button v-if=\"processEditorState.stepper.isApproveTab\" @click=\"handleFinish\"\n                :disabled=\"disableFinishButton\">Finish</button>\n        </div>\n    </div> -->\n</template>\n<style scoped>\n.process-editor {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 